import moveVerify from '@/components/helang-moveVerify/helang-moveVerify.vue';
import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	navBar,
	appid,
	appsecret
} = app;
export default {
	components: {
		moveVerify
	},
	data() {
		return {
			navBar: navBar,
			nickname: '',
			mobile: '',
			password: '',
			confirm_pwd: '',
			number_code: '',
			verify_code: '',
			showcode: false,
			back:true
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 滑动验证
		verifyResult() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				obj
			);
			rs.getRequests('random', params, res => {
				let data = res.data;
				if (data.code == 200) {
					this.number_code = data.data.number;
					uni.setStorageSync("laravel_session", res.header["Set-Cookie"]);
				} else {
					rs.Toast(data.msg);
				}
			});
		},
		// 获取短信验证码
		getCode() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			if (!this.mobile) {
				rs.Toast('手机号不能为空');
				return;
			}
			if (!this.number_code) {
				rs.Toast('请先拖动滑块');
				return;
			}

			let obj = {
				appid: appid,
				mobile: this.mobile,
				timeStamp: timeStamp
			};
			var that = this;
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var random_str = md5.hexMD5(appsecret + this.number_code);
			let params = Object.assign({
					sign: sign,
					secret_str: random_str,
					active: 2
				},
				obj
			);
			uni.request({
				url: app.rootUrl + "sendCode",
				method: 'POST',
				header: {
					'content-type': 'application/json', // 默认值
					'cookie': uni.getStorageSync("laravel_session")
				},
				data: params,
				success: function(res) {
					if (res.data.code == 200) {
						that.$refs.code.sendCode();
						that.number_code = res.data.data.random_str;
						rs.Toast('验证码已发送到你手机中，请注意查收');
					} else {
						rs.Toast(res.data.msg);
					}
				},
			})

		},

		register() {
			let timeStamp = Math.round(new Date().getTime() / 1000);

			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				nickname: this.nickname,
				mobile: this.mobile,
				password: this.password,
				confirm_pwd: this.confirm_pwd,
				verify_code: this.verify_code,
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			if (this.identifying) {
				obj.openid = this.identifying
			}
			let params = Object.assign({
				sign: sign,
			}, obj)
			var that = this;
			uni.request({
								url: app.rootUrl + "register",
								method: 'POST',
								data: params,
								header: {
									'content-type': 'application/json', // 默认值
									'cookie': uni.getStorageSync("laravel_session")
								},
								success: function(res) {
									if (res.data.code == 200) {
										rs.Toast( '注册成功');
											uni.navigateTo({
												url: '/login',
											})
									} else {
										rs.Toast( res.data.msg);
									}
								}
							})
		}
	}
};
